import React from 'react'
import PropTypes from 'prop-types'
import { useTheme, createUseStyles } from 'react-jss'
import { getTextFieldStyles } from 'frames-jss'
import cx from 'classnames'

// const useStyles = createUseStyles((theme) => getTextFieldStyles(theme))
const useStyles = createUseStyles((theme) => {
  const styles = getTextFieldStyles(theme)
  console.log(styles)
  return styles
})

const TextField = ({ value, disabled, invalid, line, label, caption, icon, className }) => {
  const theme = useTheme()
  const styles = useStyles(theme)

  return (
    <div className={cx(styles.textField, className, invalid && 'invalid', line && 'line')}>
      {label && <label>{label}</label>}
      <input type="text" defaultValue={value} disabled={disabled} placeholder="Placeholder" />
      {icon && <span className="icon">{icon}</span>}
      <span className="background"></span>
      {caption && <span className="caption">{caption}</span>}
    </div>
  )
}

TextField.propTypes = {
  value: PropTypes.string,
  disabled: PropTypes.bool,
  invalid: PropTypes.bool,
  line: PropTypes.bool,
  label: PropTypes.string,
  caption: PropTypes.string,
  icon: PropTypes.string,
  className: PropTypes.string,
}
TextField.defaultProps = {
  value: 'Text field',
  disabled: false,
  invalid: false,
  line: false,
}

export default TextField
